import test from 'ava';

var Client = require('./client.js'); // le fichier contenant les fonctions à tester

test('Afficher / masquer le texte', t => {
  document.body.innerHTML = '<div id="seeMoreDiv" style="display:none">Des explications très intéressantes</div><button id="seeMoreButton" onclick="seeMore()">Voir plus</button>';
  Client.seeMore();
  t.is(document.getElementById('seeMoreDiv').style.display, 'block');
  Client.seeMore();
  t.is(document.getElementById('seeMoreDiv').style.display, 'none');
});

test('Mettre à jour les choix disponibles', t => {});
